import { css } from "styled-components";
import customMedia from "../styles/customMedia";

export const sidePadding = css`
  padding-right: 60px;
  padding-left: 60px;
  ${ customMedia.lessThan("tb")`
    padding-right: 30px;
    padding-left: 30px;
  ` };
  ${ customMedia.lessThan("sp")`
    padding-right: 10px;
    padding-left: 10px;
  ` };
`;

export const topPaddingLarge = css`
  padding-top: 200px;
`;

export const topPaddingMidium = css`
  padding-top: 140px;
`;

export const bottomPadding = css`
  padding-bottom: 100px;
`;

export const mainWrapper = css`
  z-index: 0;
  max-width: 864px;
  margin: 0 auto;
`;